<template>
	<div class="ratings" ref="ratings">
		<div class="ratings-content">
			<div class="overview">
				<div class="overview-left">
					<h1 class="score">{{seller.score}}</h1>
					<div class="title">综合评分</div>
					<div class="rank">高于周边商家{{seller.rankRate}}%</div>
				</div>
				<div class="overview-right">
					<div class="score-wrapper">
						<span class="title">服务态度</span>
						<star class="star" :size="36" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore}}</span>
					</div>
					<div class="score-wrapper">
						<span class="title">商品评分</span>
						<star class="star" :size="36" :score="seller.foodScore"></star>
						<span class="score">{{seller.foodScore}}</span>
					</div>
					<div class="delivery-wrapper">
						<span class="title">送达时间</span>
						<span class="time">{{seller.deliveryTime}}分钟</span>
					</div>
				</div>
			</div>
			<split></split>
			<ratingselect @increment="incrementTotal" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="ratings"></ratingselect>
			<div class="rating-wrapper">
				<ul>
					<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in ratings" class="rating-item border-1px">
						<div class="avatar">
							<img :src="rating.avatar" width="28" height="28">
						</div>
						<div class="content">
							<h1 class="name">{{rating.username}}</h1>
							<div class="score-wrapper">
								<div class="star-wrapper">
									<star :size="24" :score="rating.score"></star>
								</div>
								<span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
							</div>
							<p class="text">{{rating.text}}</p>
							<div class="recommend" v-show="rating.recommend && rating.recommend.length">
								<span class="icon-thumb_up"></span>
								<span v-for="item in rating.recommend" class="recommend-item">{{item}}</span>
							</div>
							<div class="time">{{rating.rateTime | formatDate}}</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import BScroll from 'better-scroll';
	import star from 'components/star/star';
	import split from 'components/split/split';
	import ratingselect from 'components/ratingselect/ratingselect';
	import {formatDate} from 'common/js/data';

	const ERR_OK = 0;
	const ALL = 2;

	export default {
		props: {
			seller: {
				type: Object
			}
		},
		data() {
			return {
				ratings: [],
				showFlag:false,
				selectType: ALL,
				onlyContent: false,
				desc: {
					all: '全部',
					positive: '满意',
					negative: '不满意'
				}
			};
		},
		created() {
			this.$http.get('/api/ratings').then((response) => {
				response = response.body;
				if (response.errno === ERR_OK) {
					this.ratings = response.data;
					this.$nextTick(() => {
						if (!this.scroll) {
							this.scroll = new BScroll(this.$refs.ratings, {
								click:true
							});
						} else {
							this.scroll.refresh();
						}
					});
				}
			});
		},
		filters: {
			formatDate(time) {
				var date = new Date(time);
				return formatDate(date, 'yyyy-MM-dd hh:mm');
			}
		},
		methods: {
			incrementTotal(type, data) {
				this[type] = data;
				this.$nextTick(() => {
					this.scroll.refresh();
				});
			},
			needShow(type, text) {
				if (this.onlyContent && !text) {
					return false;
				}
				if (this.selectType === ALL) {
					return true;
				} else {
					return type === this.selectType;
				}
			}
		},
		components: {
			'star': star,
			'split': split,
			'ratingselect': ratingselect
		}
	};
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixin.styl";
	.ratings
		position:absolute
		width:100%
		top:174px
		bottom:0
		left:0
		overflow:hidden
		.overview
			display:flex
			padding:18px 0
			.overview-left
				flex:0 0 137px
				padding:6px 0
				border-right:1px solid rgba(7,17,27,0.1)
				text-align:center
				@media screen and (max-width:320px)
					flex:0 0 120px
					width:120px
 				.score
					margin-bottom:6px
					line-height:28px
					font-size:24px
					color:rgb(255,153,0)
				.title
					margin-bottom:8px
					line-height:12px
					font-size:12px
					color:rgb(7,17,27)
				.rank
					line-height:10px
					font-size:10px
					color:rgb(147,153,159)
			.overview-right
				flex:1
				padding:6px 0 6px 24px
				@media screen and (max-width:320px)
					padding-left:2px
				.score-wrapper
					margin-bottom:8px
					font-size:0
					.title
						line-height:18px
						font-size:12px
						color:rgb(7,17,27)
					.star
						display:inline-block
						vertical-align:top
						margin:0 12px
					.score
						display:inline-block
						line-height:18px
						font-size:12px
						color:rgb(255,153,0)
				.delivery-wrapper
					font-size:0
					.title
						line-height:18px
						font-size:12px
						color:rgb(7,17,27)
					.time
						margin-left:12px
						line-height:18px
						font-size:12px
						color:rgb(147,153,159)
		.rating-wrapper
			padding:0 18px
			.rating-item
				display:flex
				padding: 18px 0
				border-1px(rgba(7,17,27,0.1))
				.avatar
					flex:0 0 28px
					width:28px
					margin-right:12px
					img
						border-radius:50%
				.content
					flex:1
					display:relative
					.name
						margin-bottom:4px
						line-height:12px
						font-size:10px
						color:rgb(7,17,27)
					.score-wrapper
						margin-bottom:6px
						font-size:0
						.star-wrapper
							display:inline-block
							vertical-align:top
							margin-right:6px
						.delivery
							display:inline-block
							vertical-align:top
							line-height:12px
							font-size:10px
							color:rgb(147,153,159)
					.text
						margin-bottom:8px
						line-height:18px
						font-size:12px
						color:rgb(7,17,27)
					.recommend
						font-size:0
						.icon-thumb_up
							margin-right:8px
							line-height:16px
							font-size:12px
							color:rgb(0,160,220)
						.recommend-item
							display:inline-block
							margin-right:8px
							margin-bottom:4px
							padding:0 6px
							border:1px solid rgba(7,17,27,0.1)
							border-radius:1px
							background:#fff
							line-height:16px
							font-size:9px
							color:rgb(147,153,159)
					.time
						position:absolute
						top:18px
						right:18px
						line-height:12px
						font-size:10px
						color:rgb(147,153,159)
</style>



