<template>
	<transition name="move">
		<div v-show="showFlag" class="food" ref="food">
			<div class="food-content">
				<div class="image-header">
					<img :src="food.image">
					<div class="back" @click="hide">
						<i class="icon-arrow_lift"></i>
					</div>
				</div>
				<div class="content">
					<div class="title">{{food.name}}</div>
					<div class="detail">
						<span class="sell-count">月售{{food.sellCount}}份</span><span class="rating">好评率{{food.rating}}%</span>
					</div>
					<div class="price">
						<span class="new-price">￥{{food.price}}</span><span class="old-price" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
					</div>
					<div class="cartcontrol-wrapper">
						<v-cartcontrol :food="food"></v-cartcontrol>
					</div>
					<transition name="fade">
						<div class="buy" v-show="!food.count || food.count===0" @click="addFirst">加入购物车</div>
					</transition>
				</div>
				<v-split v-show="food.info"></v-split>
				<div class="info" v-show="food.info">
					<h1 class="title">商品介绍</h1>
					<p class="text">{{food.info}}</p>
				</div>
				<v-split></v-split>
				<div class="rating">
					<h1 class="title">商品评价</h1>
					<v-ratingselect @increment="incrementTotal" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></v-ratingselect>
				</div>
				<div class="rating-wrapper">
					<ul v-show="food.ratings && food.ratings.length">
						<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item border-1px">
							<div class="user">
								<span class="name">{{rating.username}}</span>
								<img class="avatar" :src="rating.avatar" width="12" height="12">
							</div>
							<div class="time">{{rating.rateTime | formatDate}}</div>
							<p class="text">
								<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span><span>{{rating.text}}</span>
							</p>
						</li>
					</ul>
					<div class="no-rating" v-show="!food.ratings || !food.rating.length">暂无评价</div>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	import Vue from 'vue';
	import BScroll from 'better-scroll';
	import cartcontrol from 'components/cartcontrol/cartcontrol';
	import split from 'components/split/split';
	import ratingselect from 'components/ratingselect/ratingselect';
	import {formatDate} from 'common/js/data';
	// const POSITIVE = 0;
	// const NEGATIVE = 1;
	const ALL = 2;

	export default {
		props: {
			food: {
				type: Object
			}
		},
		data() {
			return {
				showFlag:false,
				selectType: ALL,
				onlyContent: false,
				desc: {
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
				}
			};
		},
		methods: {
			show() {
				this.showFlag = true;
				this.selectType = ALL;
				this.onlyContent = true;
				this.$nextTick(() => {
					if (!this.scroll) {
						this.scroll = new BScroll(this.$refs.food, {
							click:true
						});
					} else {
						this.scroll.refresh();
					}
				});
			},
			incrementTotal(type, data) {
				this[type] = data;
				this.$nextTick(() => {
					this.scroll.refresh();
				});
			},
			hide() {
				this.showFlag = false;
			},
			addFirst(event) {
				if (!event._constructed) {
					return;// 当使用better-scroll派发事件的时候。_constructed为true
				}
				this.$emit('cartadd', event.target);
				Vue.set(this.food, 'count', 1);
			},
			needShow(type, text) {
				if (this.onlyContent && !text) {
					return false;
				}
				if (this.selectType === ALL) {
					return true;
				} else {
					return type === this.selectType;
				}
			}
		},
		filters: {
			formatDate(time) {
				var date = new Date(time);
				return formatDate(date, 'yyyy-MM-dd hh:mm');
			}
		},
		components: {
			'v-cartcontrol': cartcontrol,
			'v-split': split,
			'v-ratingselect': ratingselect
		}
	};
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixin.styl";

	.food
		position:fixed
		top:0
		left:0
		bottom:48px
		width:100%
		z-index:10
		background:#fff
		transition:all 0.3s linear
		&.move-enter,&.move-leave-active
			opacity:0
		&.move-enter-active,&.move-leave-active
			transform:translate3d(100%,0,0)
			transition:all 0.3s linear
		.food-content
			position:relative
			.image-header
				position:relative
				width:100%
				height:0
				padding-top:100%
				img
					position:absolute
					top:0
					left:0
					width:100%
					height:100%
				.back
					position:absolute
					top:10px
					left:0
					.icon-arrow_lift
						display:block
						padding:10px
						font-size:20px
						color:#fff
			.content
				position:relative
				padding:18px
				.title
					margin-bottom:8px
					line-height:14px
					font-size:14px
					font-weight:700
					color:rgb(7,17,27)
				.detail
					margin-bottom:18px
					height:10px
					line-height:10px
					font-size:10px
					color:rgb(147,153,159)
					.sell-count
						margin-right:12px
				.price
					line-height:24px
					.new-price
						margin-right:8px
						font-size:14px
						font-weight:700
						color:rgb(240,20,20)
					.old-price
						font-size:10px
						font-weight:700
						color:rgb(147,153,159)
						text-decoration:line-through
				.cartcontrol-wrapper
					position:absolute
					right:12px
					bottom:12px
				.buy
					position:absolute
					right:18px
					bottom:18px
					z-index:10
					height:24px
					line-height:24px
					padding:0 12px
					box-sizing:border-box
					font-size:10px
					border-radius:12px
					color:#fff
					background:rgb(0,160,220)
					transition:all 0.2s
					&.fade-enter,&.fade-leave-active
						opacity:0
					&.fade-enter-active,&.fade-leave-active
						transition:all 0.2s
						opacity:0
			.info
				padding:18px
				.title
					line-height:14px
					margin-bottom:6px
					font-size:14px
					color:rgb(7,17,27)
				.text
					padding:0 8px
					line-height:24px
					font-size:12px
					color:rgb(77,85,93)
			.rating
				padding-top:18px
				.title
					margin-left:18px
					line-height:14px
					font-size:14px
					color:rgb(7,17,27)
			.rating-wrapper
				padding: 0 18px
				.rating-item
					position:relative
					padding:16px 0
					border-1px(rgba(7,17,27,0.1))
					.user
						position:absolute
						top:16px
						right:0
						font-size:0
						line-height:12px
						.name
							display:inline-block
							vertical-align:top
							margin-right:6px
							font-size:10px
							color:rgb(147,153,159)
						.avatar
							border-radius:50%
					.time
						margin-bottom:6px
						line-height:16px
						font-size:10px
						color:rgb(147,153,159)
					.text
						line-height:16px
						font-size:12px
						color:rgb(7,17,27)
						.icon-thumb_up,.icon-thumb_down
							line-height:24px
							margin-right:4px
							font-size:12px
						.icon-thumb_up
							color:rgb(0,160,220)
						.icon-thumb_down
							color:rgb(147,153,159)
				.no-rating
					padding: 16px 0
					font-size:12px
					color:rgb(147,153,159)
</style>



